
#app {
  color: #333;
  position: relative;
  height: 100vh;
  overflow: hidden;
}
#app .tap .tap-list {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  height: 0.4rem;
  padding: 0.3em 0;
  line-height: 0.4rem;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -ms-flex-wrap: nowrap;
      flex-wrap: nowrap;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
#app .tap .tap-list .tap-item {
  -webkit-box-flex: 1;
      -ms-flex: 1 1 auto;
          flex: 1 1 auto;
  text-align: center;
  cursor: pointer;
  font-size: 0.14rem;
  color: #4d555d;
}
#app .tap .tap-list .tap-item.router-link-active {
  color: #f01414;
}


