
.food-item-detail {
  display: block;
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0.56rem;
  z-index: 33;
  background-color: #fff;
}
.food-item-detail.move-enter,
.food-item-detail.move-leave-to {
  -webkit-transform: translate3d(100%, 0, 0);
          transform: translate3d(100%, 0, 0);
}
.food-item-detail.move-enter-active,
.food-item-detail.move-leave-active {
  transition: all 0.2s linear;
}
.food-item-detail.move-enter-to,
.food-item-detail.move-leave {
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
}
.food-item-detail .food-content .block-title {
  color: #07111b;
  font-size: 0.14rem;
  line-height: 0.14rem;
  margin-bottom: 0.06rem;
}
.food-item-detail .food-content .image-header {
  display: block;
  position: relative;
  width: 100%;
  padding-top: 100%;
  height: 0;
}
.food-item-detail .food-content .image-header img {
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.food-item-detail .food-content .image-header .back {
  display: block;
  padding: 0.1rem;
  position: absolute;
  top: 0.1rem;
  left: 0;
  font-size: 0.2rem;
  color: #fff;
}
.food-item-detail .food-content .main {
  position: relative;
  padding: 0.18rem;
  background-color: #fff;
}
.food-item-detail .food-content .main .title {
  font-weight: 700;
  margin-bottom: 0.08rem;
}
.food-item-detail .food-content .main .detail {
  display: block;
  font-size: 0;
  color: #93999f;
  line-height: 0.1rem;
  margin-bottom: 0.18rem;
}
.food-item-detail .food-content .main .detail .sell-count,
.food-item-detail .food-content .main .detail .rating-rate {
  display: inline-block;
  font-size: 0.1rem;
  margin-right: 0.12rem;
  line-height: inherit;
  vertical-align: top;
}
.food-item-detail .food-content .main .price {
  display: block;
  font-size: 0;
  font-weight: 700;
  line-height: 0.24rem;
}
.food-item-detail .food-content .main .price .now,
.food-item-detail .food-content .main .price .old {
  display: inline-block;
  vertical-align: top;
  line-height: inherit;
}
.food-item-detail .food-content .main .price .now {
  margin-right: 0.08rem;
  font-size: 0.14rem;
  color: #f01414;
}
.food-item-detail .food-content .main .price .old {
  font-size: 0.1rem;
  text-decoration: line-through;
  color: #93999f;
}
.food-item-detail .food-content .main .cartcontrol-wrap {
  position: absolute;
  right: 0.12rem;
  bottom: 0.12rem;
}
.food-item-detail .food-content .main .buy-button {
  position: absolute;
  right: 0.18rem;
  bottom: 0.18rem;
  z-index: 10;
  height: 0.24rem;
  line-height: 0.24rem;
  padding: 0 0.12rem;
  border-radius: 0.12rem;
  background-color: #00a0dc;
  color: #fff;
  opacity: 1;
  font-size: 0.1rem;
  overflow: hidden;
}
.food-item-detail .food-content .main .buy-button.fade-enter,
.food-item-detail .food-content .main .buy-button.fade-leave-to {
  opacity: 0;
  z-index: -1;
}
.food-item-detail .food-content .main .buy-button.fade-enter-active,
.food-item-detail .food-content .main .buy-button.fade-leave-active {
  transition: all 0.2s linear;
}
.food-item-detail .food-content .main .buy-button.fade-enter-to,
.food-item-detail .food-content .main .buy-button.fade-leave {
  opacity: 1;
}
.food-item-detail .info {
  padding: 0.18rem;
  background-color: #fff;
}
.food-item-detail .info .text {
  line-height: 0.24rem;
  font-weight: 200;
  padding: 0 0.08rem;
  color: #4d555d;
}
.food-item-detail .rating {
  padding-top: 0.18rem;
}
.food-item-detail .rating .title {
  margin-left: 0.18rem;
}
.food-item-detail .rating .rating-cont-wrap .cont-list {
  padding-left: 0.18rem;
  padding-right: 0.18rem;
}
.food-item-detail .rating .rating-cont-wrap .cont-list .rating-cont-item {
  position: relative;
  padding: 0.16rem 0;
  position: relative;
}
.food-item-detail .rating .rating-cont-wrap .cont-list .rating-cont-item:after {
  display: block;
  content: ' ';
  width: 100%;
  height: 0;
  position: absolute;
  left: 0;
  bottom: 0;
  border-bottom: 1px solid rgba(7,17,27,0.1);
}
.food-item-detail .rating .rating-cont-wrap .cont-list .rating-cont-item .item-user {
  display: block;
  position: absolute;
  top: 0.16rem;
  right: 0;
  color: #93999f;
  font-size: 0;
  line-height: 0.12rem;
}
.food-item-detail .rating .rating-cont-wrap .cont-list .rating-cont-item .item-user .user-name,
.food-item-detail .rating .rating-cont-wrap .cont-list .rating-cont-item .item-user .avatar {
  display: inline-block;
  vertical-align: top;
  line-height: inherit;
}
.food-item-detail .rating .rating-cont-wrap .cont-list .rating-cont-item .item-user .user-name {
  font-size: 0.1rem;
  margin-right: 0.06rem;
}
.food-item-detail .rating .rating-cont-wrap .cont-list .rating-cont-item .item-user .avatar {
  width: 0.12rem;
  height: 0.12rem;
  border-radius: 50%;
}
.food-item-detail .rating .rating-cont-wrap .cont-list .rating-cont-item .item-time {
  font-size: 0.1rem;
  color: #93999f;
  line-height: 0.12rem;
  margin-bottom: 0.06rem;
}
.food-item-detail .rating .rating-cont-wrap .cont-list .rating-cont-item .item-text {
  line-height: 0.16rem;
  color: #07111b;
}
.food-item-detail .rating .rating-cont-wrap .cont-list .rating-cont-item .item-text .icon-thumb_up {
  color: #00a0dc;
}
.food-item-detail .rating .rating-cont-wrap .cont-list .rating-cont-item .item-text .icon-thumb_down {
  color: #93999f;
}
.food-item-detail .rating .rating-cont-wrap .cont-list .rating-cont-item .item-text [class^="icon-thumb"],
.food-item-detail .rating .rating-cont-wrap .cont-list .rating-cont-item .item-text .text-cont {
  display: inline-block;
  vertical-align: top;
  line-height: inherit;
}
.food-item-detail .rating .rating-cont-wrap .cont-list .rating-cont-item .item-text .icon-thumb {
  font-size: 0.12rem;
}
.food-item-detail .rating .rating-cont-wrap .cont-list .rating-cont-item .item-text .text-cont {
  font-size: 0.12rem;
  color: #07111b;
}


