
.clearfix:after,
.clearfix:before {
  content: "";
  display: table;
  overflow: hidden;
}
.clearfix:after {
  height: 0;
  line-height: 0;
  clear: both;
}
.center-block {
  display: block;
  margin-right: auto;
  margin-left: auto;
}
@media (-webkit-min-device-pixel-ratio: 1.5), (min-device-pixel-ratio: 1.5) {
.border-bottom-1px:after {
    -webkit-transform: scaleY(0.7);
            transform: scaleY(0.7);
}
.border-left-1px:after {
    -webkit-transform: scaleX(0.7);
            transform: scaleX(0.7);
}
.border-right-1px:after {
    -webkit-transform: scaleX(0.7);
            transform: scaleX(0.7);
}
}
@media (-webkit-min-device-pixel-ratio: 2), (min-device-pixel-ratio: 2) {
.border-bottom-1px:after {
    -webkit-transform: scaleY(0.5);
            transform: scaleY(0.5);
}
.border-left-1px:after {
    -webkit-transform: scaleX(0.5);
            transform: scaleX(0.5);
}
.border-right-1px:after {
    -webkit-transform: scaleX(0.5);
            transform: scaleX(0.5);
}
}
.ratingselect .rating-type {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-flow: row nowrap;
          flex-flow: row nowrap;
  -webkit-box-pack: start;
      -ms-flex-pack: start;
          justify-content: flex-start;
  -ms-flex-line-pack: center;
      align-content: center;
  margin: 0 0.18rem;
  padding: 0.12rem 0 0.18rem;
  position: relative;
}
.ratingselect .rating-type:after {
  display: block;
  content: ' ';
  width: 100%;
  height: 0;
  position: absolute;
  left: 0;
  bottom: 0;
  border-bottom: 1px solid rgba(7,17,27,0.1);
}
.ratingselect .rating-type .block {
  color: #4d555d;
  font-size: 0;
  line-height: 0.16rem;
  padding: 0.08rem 0.12rem;
  margin-right: 0.08rem;
  background-color: rgba(0,160,220,0.2);
}
.ratingselect .rating-type .block:last-child {
  margin-right: 0;
}
.ratingselect .rating-type .block.negative {
  background-color: rgba(77,85,93,0.2);
}
.ratingselect .rating-type .block.active {
  color: #fff;
}
.ratingselect .rating-type .block.positive.active {
  background-color: #00a0dc;
}
.ratingselect .rating-type .block.negative.active {
  background-color: #4d555d;
}
.ratingselect .rating-type .block .select-cont,
.ratingselect .rating-type .block .rating-count {
  display: inline-block;
  vertical-align: top;
  line-height: inherit;
}
.ratingselect .rating-type .block .select-cont {
  font-size: 0.12rem;
}
.ratingselect .rating-type .block .rating-count {
  font-size: 0.08rem;
  margin-left: 0.02rem;
}
.ratingselect .switch {
  padding: 0.12rem 0.18rem;
  line-height: 0.24rem;
  font-size: 0;
  color: #93999f;
  border-bottom: 1px solid rgba(7,17,27,0.1);
}
.ratingselect .switch.on [class^=icon-check] {
  color: #00a0dc;
}
.ratingselect .switch [class^=icon-check],
.ratingselect .switch .text {
  display: inline-block;
  vertical-align: top;
  line-height: inherit;
}
.ratingselect .switch [class^=icon-check] {
  font-size: 0.24rem;
  margin-right: 0.04rem;
}
.ratingselect .switch .text {
  font-size: 0.12rem;
}


